* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

.App {
  position: relative;
  background: #fafafa;
  font-family: Bogle;
}

.checkOutSection {
  width: 222px;
  position: absolute;
  top: 10px;
  right: 10px;
  background: #fff;
  padding: 10px;
  border: 1px solid #e6e7e8;
  border-bottom-left-radius: 8px;
  border-bottom-right-radius: 8px;
  -webkit-box-shadow: 0 2px 3px 0 rgba(0,0,0,.16);
          box-shadow: 0 2px 3px 0 rgba(0,0,0,.16);
}

.tile:after, .tile:before {
    content: "";
    display: table;
}

.tile:after {
    clear: both;
}

.tile:first-child {
  margin-top: 0;
}

.tile:last-child {
  border-top: 1px solid #e6e7e8;
  padding: 10px 0;
  font-size: 1.25rem;
  font-weight: 700;
  line-height: 1.5em;
}

.last {
  font-size: 1.25rem;
}

.tileContainer {
  color: #000;
}

.tile {
  margin: 6px 0;
  font-size: .875rem;
  font-weight: 400;
}

.pickup-label {
  text-decoration: underline;
}

.tile-label {
  float: left;
  max-width: 65%;  
}

.tile-value {
  float: right;
  font-weight: 700;
  color: #000;
}

.discount {
  color: #de1c24;
}

.item-button {
  margin: 10px 0;
  font-size: .875rem;
  position: relative;
  padding: 0;
  height: 20px;
  color: #000;
  background: #fff;
  cursor: pointer;
  line-height: 1;
  -moz-osx-font-smoothing: grayscale;
  -webkit-font-smoothing: antialiased;
  border: 0;
  -webkit-appearance: button;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
  -webkit-transition-property: background-color,opacity,border-color,color;
  -o-transition-property: background-color,opacity,border-color,color;
  transition-property: background-color,opacity,border-color,color;
  -webkit-transition-duration: .12s;
       -o-transition-duration: .12s;
          transition-duration: .12s;
  -webkit-transition-timing-function: ease-out;
       -o-transition-timing-function: ease-out;
          transition-timing-function: ease-out;   
}

.item-button-icon {
  margin-left: 10px;
  font-size: 1.2rem;
}

.item-button-text {
  text-decoration: underline;
}

.item-thumbnail {
  display: inline-block;
  width: 40%;
}

.item-thumbnail img {
  width: 67px;
}

.item-extra-details {
  width: 85%;
}

.original-content {
  display: inline-block;
  width: 60%;
  font-size: .875rem;
  line-height: 18px;
  position: absolute;
}

.original-content span {
  word-wrap: break-word;
}

.float-left {
  float: left;
}

.float-right {
  float: right;
}

.item-container {
  margin-bottom: 10px;
}

.promo-container {
  padding: 7px 0;
  border-top: 1px solid #e6e7e8;
}

.promo-input {
  border: 1px solid #e6e7e8;
  font-size: .75rem;
  color: #222;
  padding: 0 10px;
  line-height: 2.375em;
  border-radius: 4px;
  font-size: .75rem;
  background-color: #fff;
  width: 55%;
  height: 30px;
  display: inline-block;
}

.promo-button {
  display: inline-block;
  width: 30%;
  height: 30px;
  padding: 0;
  margin-top: 0;
  color: #000;
  border: 1px solid #000;
  background-color: #fff;
  border-radius: 14px;
  white-space: nowrap;
  overflow: hidden;
  -o-text-overflow: ellipsis;
     text-overflow: ellipsis;
  -webkit-font-smoothing: antialiased;
  position: absolute;
  margin-left: 10px;
} 

.promo-label {
  margin-left: 0;
  white-space: nowrap;
  font-size: .875rem;
  padding: 0 5px;
}

.item-content {
  color: #6d6e71;
}

.invalid-promo {
  color: #de1c24;
}

.invalid-promo input {
  border: 1px solid #de1c24;
}

.tooltip {
  position: absolute;
  top: 61px;
  right: 60px;
  width: 203px;
  height: 120px;
  -webkit-box-shadow: 0 0 7px 0 rgba(0,0,0,.17);
          box-shadow: 0 0 7px 0 rgba(0,0,0,.17);
  background: #fff;
  text-align: center;
  border: 1px solid #e6e7e8;
  z-index: 200;
  padding: 24px;
  -webkit-animation: j .3s ease forwards;
          animation: j .3s ease forwards;
}

.tooltip:before {
  content: "";
  position: absolute;
  top: -10px;
  left: 90px;
  pointer-events: none;
  width: 18px;
  height: 18px;
  background-color: #fff;
  border: 1px solid rgba(0,0,0,.17);
  border-left: none;
  border-bottom: none;
  border-width: 0 0 1px 1px;
  -webkit-box-shadow: 0 -1px 2px -1px rgba(0,0,0,.17);
          box-shadow: 0 -1px 2px -1px rgba(0,0,0,.17);
  -webkit-transform: rotate(-45deg);
      -ms-transform: rotate(-45deg);
          transform: rotate(-45deg);
}


